'use client'
import { Navigation } from "swiper/modules";
import { Swiper, SwiperSlide } from "swiper/react";

interface NearbyLocation {
  name: string;
  distance: string;
  unit: string;
  image: string;
  altText?: string;
}

interface LocationProps {
  project: {
    projectLogo: string;
    projectName: string;
    locationType: string;
    locationIframe?: string;
    locationImage?: string;
    locationAltText?: string;
    nearbyLocations?: NearbyLocation[];
  };
}

export default function ProjectLocation({ project }: LocationProps) {
  return (
    <div className="w-100 padding overflow-hidden position-relative location-section bg-light">
      <p className="hidden-title" data-position="right">Location</p>
      <div className="container-lg">
        <div className="heading mx-auto text-center">
          <h4 className="fw-light text-primary">
            Locations that promise value today and tomorrow.
          </h4>
          <h2 className="title-xxl mb-0">At the Heart of Growth</h2>
        </div>

        <div className="mapBox position-relative mb-4">
          <div className="mapIcon position-absolute">
            <img src={project.projectLogo} alt={project.projectName} />
          </div>

          {project.locationType === 'iframe' && project.locationIframe ? (
            <iframe
              src={project.locationIframe}
              width="100%"
              height="450"
              style={{ border: 0 }}
              allowFullScreen
              loading="lazy"
              referrerPolicy="no-referrer-when-downgrade"
            ></iframe>
          ) : project.locationImage ? (
            <img
              src={project.locationImage}
              alt={project.locationAltText || project.projectName}
              className="w-100"
              style={{ height: '450px', objectFit: 'cover' }}
            />
          ) : null}
        </div>

        {project.nearbyLocations && project.nearbyLocations.length > 0 && (
          <Swiper
            modules={[Navigation]}
            spaceBetween={20}
            slidesPerView={2}
            navigation
            breakpoints={{
              0: { slidesPerView: 1 },
              768: { slidesPerView: 2 },
              1024: { slidesPerView: 3 },
            }}
            loop
            className="connectivity-slider"
          >
            {project.nearbyLocations.map((location, index) => (
              <SwiperSlide key={index} className="location-icon">
                <div className="in text-center">
                  <div className="img-fluid p-0 mb-2">
                    <img src={location.image} alt={location.altText || location.name} />
                  </div>
                  <div className="location-text">
                    <p>
                      <span className="h4">{location.distance}</span>
                      <small>
                        {location.unit} <br />Drive From
                      </small>
                    </p>
                    <p className="mb-0">{location.name}</p>
                  </div>
                </div>
              </SwiperSlide>
            ))}
          </Swiper>
        )}
      </div>
    </div>
  );
}
